import RenDropdownFilter from '@/components/ren-dropdown-filter/ren-dropdown-filter.vue'
import uniSwiperDot from "@/components/uni-swiper-dot/uni-swiper-dot.vue"
import lModal from '@/components/l-modal/l-modal.vue'
import avatar from "../../../components/yq-avatar/yq-avatar.vue";
export default {
	components: {
		RenDropdownFilter,
		uniSwiperDot,
		lModal,
		avatar
	},
	data() {
		return {
			filterData: [
				[{
					text: '1-10',
					value: 1
				}, {
					text: '11-20',
					value: 2
				}],
				[{
					text: '系统图片',
					value: 1
				}, {
					text: '用户上传图片',
					value: 2
				}],
				[{
					text: '系统备注',
					value: 1
				}, {
					text: '用户上传备注',
					value: 2
				}],
				[{
					text: '顺序',
					value: 1
				}, {
					text: '倒叙',
					value: 2
				}, {
					text: '乱序',
					value: 3
				}]
			],
			defaultIndex: [0, 0, 0, 0],
			indicatorDots: true,
			autoplay: false,
			circular: true,
			interval: 2000,
			duration: 500,
			dotsStyles: {
				color: 'black',
			},
			swiperList: [{
					imgSrc: '../../../static/img/icons/zhongjiang.png',
					title: '一件衣服',
					num: 1
				},
				{
					imgSrc: '../../../static/img/icons/shandian.png',
					title: '两只袜子',
					num: 2
				},
				{
					imgSrc: '../../../static/img/icons/zhongjiang.png',
					title: '一件衣服',
					num: 1
				},
				{
					imgSrc: '../../../static/img/icons/shandian.png',
					title: '两只袜子',
					num: 2
				},
				{
					imgSrc: '../../../static/img/icons/zhongjiang.png',
					title: '一件衣服',
					num: 1
				},
				{
					imgSrc: '../../../static/img/icons/shandian.png',
					title: '两只袜子',
					num: 2
				},
			],
			current: 0,
			swiperHeight: '', // swpier的高度
			cloneHeight: '', //站位标签高度
			showCard: false, // 是否展示card格式
			isShowModal: false, //弹窗部分
			modalTitle: '提示',
			userRemark: '', //自定义备注
			btnType: 'remark', // 判断点击的是备注还是图片
			modalImg:'',
			avatarShow: false
		}
	},
	mounted() {
		let vm = this
		uni.getSystemInfo({
			success: function(res) {
				let obj = uni.createSelectorQuery().select('.img-card-btn')
				obj.boundingClientRect(function(data) {
					let objSelect = uni.createSelectorQuery().select('.select-box')
					objSelect.boundingClientRect(function(response) {
						// console.log('windowHeight',res.windowHeight) 
						// console.log('data.height',data.height) 
						// console.log('response.height',response.height) 
						vm.swiperHeight = (res.windowHeight - data.height - response.height - 20) * 2
						vm.cloneHeight = data.height * 2
					}).exec()
				}).exec()
			}
		});
	},
	methods: {
		onSelected(res) {
			console.log(res)
		},
		change(e) {
			this.current = e.detail.current;
		},
		changeType() {
			this.showCard = !this.showCard
		},
		gotoUrl() {
			uni.navigateTo({
				url: '/pages/train/imageMemory/numImgTest'
			})
		},
		openPop(type) {
			this.btnType = type
			if (this.btnType == 'remark') {
				this.modalTitle = '自定义备注'
			} else {
				this.modalTitle = '自定义图片'
			}
			this.isShowModal = true
		},
		cancel() {
			this.isShowModal = false
			if (this.btnType == 'remark') {
				this.userRemark = ''
			} else {
				this.modalImg = ''
				this.avatarShow = false
			}
		},
		confirm() {
			if (this.btnType == 'remark') {
				this.userRemark = ''
			} else {
				this.avatarShow = false
				this.modalImg = ''
			}
			this.isShowModal = false
		},
		myUpload(rsp) {
			// this.url = rsp.path; //更新头像方式一
			//rsp.avatar.imgSrc = rsp.path; //更新头像方式二
			// console.log('00000000000000', rsp)
			this.avatarShow = false
			this.modalImg = rsp.base64
		},
		openavatar(index) {
			this.avatarShow = true
			this.$refs.avatar.fChooseImg(index, {
				selWidth: "300upx",
				selHeight: "300upx",
				expWidth: '260upx',
				expHeight: '260upx'
			});
		}
	}
}
